html {
  font-family: Open Sans, Arial, sans-serif;
  font-size: 17px;
}

body {
  --whiteTextColor: #efd6ac;
  background-color: #183a37;
  margin-top: 2.5rem;
}

h1 {
  text-align: center;
  color: var(--whiteTextColor);
  margin-top: .6rem;
  margin-bottom: 0;
  font-size: 1.1rem;
  display: none;
}

p {
  text-align: center;
  color: var(--whiteTextColor);
}

a {
  color: #ffc439;
}

input {
  accent-color: #6e6e6e;
}

input[type="checkbox"], input[type="radio"], input[type="checkbox"] + label, input[type="radio"] + label {
  cursor: pointer;
}

.streak p {
  margin: 0;
  font-size: .65rem;
}

#current-streak {
  float: left;
  left: .23rem;
}

#current-streak-text, #max-streak-text {
  margin: 0;
  font-size: 1rem;
}

.grow-animation {
  animation: .5s grow-animation;
}

@keyframes grow-animation {
  from {
    transform: scale(1.7);
  }

  to {
    transform: scale(1);
  }
}

#max-streak {
  float: right;
  right: .23rem;
}

#toppest-container {
  margin: auto;
}

#top-container {
  background: #1a403d;
  border-radius: .64rem;
  margin: .8rem;
  padding: .35rem .6rem;
  box-shadow: .3rem .3rem .36rem #1c1c1c;
}

#streak-container:after, #settings-child-container:after {
  content: "";
  clear: both;
  display: table;
}

#streak-container {
  padding: .3rem .4rem .4rem;
}

#verb-box {
  border-radius: 1.14rem;
  padding: .45rem;
  display: inline-block;
  position: relative;
}

#verb-box p {
  margin-left: 1.18rem;
  margin-right: 1.18rem;
}

#verb-container {
  text-align: center;
}

#verb-text {
  justify-content: center;
  align-items: flex-end;
  margin-top: 0;
  margin-bottom: .25rem;
  font-size: 2.2rem;
  line-height: 1;
  display: flex;
}

ruby {
  flex-direction: column-reverse;
  display: inline-flex;
}

span.rt {
  font-size: .5em;
  line-height: 1;
}

#translation {
  margin-top: 0;
  margin-bottom: .18rem;
  font-size: .8rem;
}

#verb-type {
  margin-top: .6rem;
  margin-bottom: .15rem;
  font-size: .8rem;
  line-height: 1;
}

#conjugation-inquery-text {
  margin-top: .8rem;
  margin-bottom: .5rem;
  font-size: 1.3rem;
}

#status-container {
  text-align: center;
  min-height: 4rem;
}

#status-box {
  background: green;
  border-radius: .91rem;
  flex-direction: column;
  justify-content: center;
  min-width: 66%;
  min-height: 4rem;
  display: inline-flex;
}

#status-text {
  vertical-align: middle;
  color: #fff;
  margin: .3rem .8rem;
  font-size: 1.09rem;
  line-height: 1.5;
}

.sub-conjugation-indicator {
  padding-left: .3rem;
  font-size: .8rem;
  display: inline-block;
}

#input-container {
  text-align: center;
  margin: .45rem .45rem .3rem;
  padding-top: .36rem;
  position: relative;
}

.tooltip-fade-animation {
  animation: 6s tooltip-fade-animation;
}

@keyframes tooltip-fade-animation {
  0% {
    opacity: 0;
  }

  6% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.tooltip {
  color: #fff;
  opacity: 0;
  background: red;
  border-radius: .3rem;
  padding: 0 .4rem;
  font-size: .66rem;
  position: absolute;
  bottom: 100%;
  left: 7%;
}

.tooltip:after {
  content: "";
  border: 5px solid #0000;
  border-top-color: red;
  margin-left: -5px;
  position: absolute;
  top: 100%;
  left: 14%;
}

#main-text-input, #settings-child-container {
  width: 88%;
}

#main-text-input {
  background: #fff;
  border: .068rem;
  border-radius: 5rem;
  padding: .27rem .45rem .18rem;
  font-size: 1rem;
}

#main-text-input:hover:not(:focus):not(:disabled) {
  background: #e0e0e0;
}

#main-text-input:disabled {
  background: #969696;
}

#settings-container {
  text-align: center;
  margin: 0;
}

#settings-child-container {
  display: inline-block;
}

#settings-table {
  table-layout: fixed;
  width: 100%;
}

button {
  float: right;
  color: var(--whiteTextColor);
  background: #3f3f3f;
  border: none;
  border-radius: .4rem;
  margin-right: -.27rem;
  padding: .18rem .27rem .18rem .55rem;
  font-size: .73rem;
  box-shadow: 0 .045rem #232323, .045rem 0 #232323, .09rem .045rem #232323, .045rem .09rem #232323, .136rem .09rem #232323, .09rem .136rem #232323;
}

#back-button {
  padding-right: .4rem;
