button:hover {
  cursor: pointer;
  background: #464646;
}

button:disabled {
  color: #878787;
  background: #282828;
}

#press-any-key-text {
  color: var(--whiteTextColor);
  margin: 0;
  font-size: .64rem;
}

h2 {
  text-align: center;
  color: var(--whiteTextColor);
  margin-top: .1rem;
  margin-bottom: .45rem;
  font-size: 1rem;
}

#options-view {
  padding-left: .73rem;
  padding-right: .73rem;
}

#options-form {
  text-align: left;
  color: var(--whiteTextColor);
  font-size: .8rem;
}

#options-form .option-row {
  margin-bottom: .18rem;
}

#options-form .sub-option-row {
  margin-bottom: .09rem;
}

#top-must-choose {
  max-width: 70%;
}

.settings-error-text {
  color: #ec3333;
  text-align: right;
  font-size: .6rem;
}

#options-form td:not(.settings-error-text) {
  width: 35%;
}

#options-form input {
  width: .6rem;
  min-width: 13px;
  height: .6rem;
  min-height: 13px;
}

#options-form label {
  padding-left: .25rem;
  line-height: 1.4;
}

.options-indent {
  padding-left: 1.36rem;
}

#back-button-container {
  width: 100%;
  margin-top: .45rem;
  display: inline-block;
}

#options-form h3 {
  margin-top: 0;
  margin-bottom: 0;
  display: inline-block;
}

.transparent-furigana .rt, .transparent {
  opacity: 0;
}

.hide-furigana .rt, .display-none, .hide-emojis .inquery-emoji {
  display: none !important;
}

.background-none {
  background: none !important;
}

hr {
  border: .045rem solid #424242;
  margin: .18rem;
}

#adjectives-h3-container, #verbs-h3-container {
  margin-top: .45rem;
  margin-bottom: .45rem;
}

.footer-text {
  color: gray;
  margin-top: .3rem;
  margin-bottom: .1rem;
  font-size: .61rem;
}

#donation-section {
  text-align: center;
  margin: .6rem;
}

.conjugation-inquery {
  display: inline-block;
}

.inquery-emoji {
  font-size: .7rem;
  line-height: 1.1;
}

.inquery-text {
  line-height: 1.1;
}

.advanced-section {
  border: 2px solid #424242;
  border-radius: 10px;
  max-width: fit-content;
  margin-top: .1rem;
  margin-bottom: .5rem;
  margin-left: -.23rem;
  padding: .2rem .4rem .2rem .3rem;
  box-shadow: .14rem .1rem 5px #232323, .1rem .14rem 5px #232323;
}

.advanced-option {
  margin-bottom: .2rem;
}

#advanced-text {
  color: #6e6e6e;
  align-items: center;
  margin-bottom: .2rem;
  padding-left: .2rem;
  font-size: .7em;
  font-style: italic;
  font-weight: 600;
  display: flex;
}

.question-screen #verb-type, .results-screen #verb-box.background-none #verb-type {
  display: none;
}

.question-screen #status-container {
  min-height: .8rem;
}

@media screen and (height >= 820px), screen and (width >= 510px) {
  .question-screen #verb-type, .results-screen #verb-box.background-none #verb-type {
    display: block;
  }

  .question-screen #status-container {
    min-height: 4rem;
  }
}

@media screen and (width >= 510px) {
  html {
    font-size: 18px;
  }

  #toppest-container {
    width: 510px;
  }

  #top-container {
    padding: .6rem;
  }

  h1 {
    margin-top: .9rem;
    margin-bottom: .9rem;
    font-size: 1.4rem;
    display: block;
  }

  #input-container {
    margin-bottom: .4rem;
  }

  #conjugation-inquery-text {
    margin-top: 1rem;
  }

  #status-container {
    margin: 1rem;
  }
}

@media screen and (width >= 750px) {
  html {
    font-size: 20px;
  }

  #toppest-container {
    width: 575px;
  }
}

@media screen and (width >= 1100px) {
  html {
    font-size: 22px;
  }

  #toppest-container {
    width: 650px;
  }

  h1 {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}

.options-group td, th {
  border: 2px solid #04151f;
  padding: 5px 10px;
}

td a, th a {
  text-decoration: none;
}

#example-explanation {
  text-align: center;
  padding-bottom: 1rem;
  display: block;
